import React, { Component } from 'react';
import './App.css';

class App extends Component {
  state={
    today: new Date(),
    color:'#66ff99',
  }
  changetime=()=>{
    this.setState({
      today:new Date()
    })
  }
  keepcalling(){
  setInterval(() => 
    this.changetime()
  , 1000);
}
thecolor=(colour)=>{
  this.setState({color:colour
});
}
render(){
    this.keepcalling();
    const monthNames = ["January", "February", "March", "April", "May", "June",
  "July", "August", "September", "October", "November", "December"
];
    const weekNames=['Sun','Mon','Tue','Wed','Thurs','Fri','Sat'];
    //let boxclass=["notborder"];
    //if(this.state.addClass){
     // boxclass.push('border')
    //}
    let month=this.state.today.getMonth();
    let date=this.state.today.getDate();
    let year=this.state.today.getFullYear();
    let hour=this.state.today.getHours();
    let min=this.state.today.getMinutes();
    let sec=this.state.today.getSeconds();
    let week=this.state.today.getDay();
    let ampm = (hour >= 12 ? 'pm' : 'am');
    hour = hour % 12;
    hour = hour ? hour : 12;
    min = min < 10 ? '0'+min : min;
    hour = hour < 10 ? '0'+hour : hour;
    sec = sec < 10 ? '0'+sec : sec;
    let time=`${hour}:${min}:${sec}`;
    return (
      <div className="App">
        <div className="date-time">
    <div 
    style={{color:this.state.color}} 
    className="date">{monthNames[month]} {date}, {year}</div>
             <div className="weeks">
               <div className={"week Sun"+weekNames[week]}>Sun</div>
               <div className={"week Mon"+weekNames[week]}>Mon</div>
               <div className={"week Tue"+weekNames[week]}>Tue</div>
               <div className={"week Wed"+weekNames[week]}>Wed</div>
               <div className={"week Thurs"+weekNames[week]}>Thurs</div>
               <div className={"week Fri"+weekNames[week]}>Fri</div>
               <div className={"week Sat"+weekNames[week]}>Sat</div>
             </div>
             <div className="time-ampm">
               <div className="wrapper-h1">
              <h1
              style={{color:this.state.color}} 
              >{time}</h1>
              </div>
              <h2
              style={{color:this.state.color}} 
              >{ampm}</h2>
             </div>
       </div>
       <div className="colors">
         <div className={"whichcolor c1 "} onClick={()=>{this.thecolor('#66ff99')}} ></div>
         <div className={"whichcolor c2 "} onClick={()=>{this.thecolor('#111199')}}></div>
         <div className={"whichcolor c3 "} onClick={()=>{this.thecolor('#7a2121')}}></div>
         <div className={"whichcolor c4 "} onClick={()=>{this.thecolor('#e20606')}}></div>
         <div className={"whichcolor c5 "} onClick={()=>{this.thecolor('#13cc13')}}></div>
         <div className={"whichcolor c6 "} onClick={()=>{this.thecolor('#ff00f2')}}></div>
         <div className={"whichcolor c7 "} onClick={()=>{this.thecolor('#5f5d5d')}}></div>
         <div className={"whichcolor c8 "} onClick={()=>{this.thecolor('#ebeb13')}}></div>
       </div>
      </div>
    );
  }
}

export default App;



